<template>
  <div>
    <div class="cursor" ref="n-cursor"></div>
  </div>   
</template>


<script>
import $ from 'jquery';

export default {
    name: "CustomCursor",
    mounted(){
          $(function () {
        $("body").mousemove(function(e) {
          $(".cursor").show().css({
            "left": e.pageX - 5,
            "top": e.pageY - 5 
          });
        }).mouseout(function() {
          $(".cursor").hide();
        });
        });
        
         
        $(function () {
        $("body").mousedown(function(e) {
          $(".cursor").css({
            "width": "15px",
            "height": "15px",
            "left": e.pageX - 5,
            "top": e.pageY - 5,
          });
        }).mouseup(function (e) {
          $(".cursor").css({
            "width": "10px",
            "height": "10px",
            "left": e.pageX - 5,
            "top": e.pageY -5,
          });
        });
        });

        $(function () {
          $(".pointer-link").mouseover(function(){
            $(".cursor").addClass("link-hover");
          }).mouseleave(function(){
              $(".cursor").removeClass("link-hover");
          })
        })

      
    }
  
}







</script>

<style scoped>


.home .cursor {
  background-color: black;
}



.cursor {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  position: absolute;
  z-index: 200;
  pointer-events: none;
  transition: height .5s, width .5s;
}

.about-container .cursor {
  background-color: black;
}

.cursor.link-hover {
  width: 30px !important;
  height: 30px !important;
  
  

}

.romance-book .cursor {
  background-color: var(--darkred);
  border: 1px solid var(--lightred);
}

.history-book .cursor  {
  background-color: var(--darkyellow);
  border: 1px solid var(--lightyellow);

}

.adventure-book  .cursor {
  background-color: var(--darkgreen);
  border: 1px solid var(--lightgreen);

}

.fantasy-book .cursor {
  background-color: var(--darkblue);
  border: 1px solid var(--lightblue);

}


    
  


